.container {
  flex: 0 0 auto;
  overflow-y: auto;
  background-color: var(--darkened-background-color);
  padding: 2em;
  width: 30vw;
  font-family: Segoe UI, sans-serif;
}

.header {
  margin-bottom: 2em;
}

.title__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.title {
  display: block;
  font-size: 1.4em;
  text-decoration: none;
  color: white;
  margin-bottom: 0.5em;
  font-family: "Jetbrains Mono", sans-serif;
  width: fit-content;
  max-width: 40ch;
  overflow: hidden;
  text-overflow: ellipsis;
}

.title:hover {
  text-decoration: underline;
  color: aquamarine;
}

.close_icon {
  margin-left: auto;
  cursor: pointer;
}

.close_icon:hover {
  opacity: 0.85;
}

.description {
  display: inline-block;
  line-height: 1.2;
}

.meta {
  display: block;
  font-size: 1.1em;
  list-style: none;
  padding-left: 0;
  margin: 2em 0;
}

.meta b {
  font-weight: 700;
}

.meta > span {
  font-size: 1.4em;
}

.wikiVgEntry {
  margin: 2em 0;
  display: flex;
  flex-direction: column;
}

.wikiVgTitle {
  background-color: var(--lightened-background-color);
  border-bottom: 0;
  border-top-left-radius: 13px;
  border-top-right-radius: 13px;
  padding: 0.5em;
  width: fit-content;
}

.wikiVgContent {
  border: 2px solid var(--lightened-background-color);
  border-radius: 13px;
  border-top-left-radius: 0;
  padding: 1em;
}

.fields {
  width: 100%;
}

.extra {
  margin-top: 3em;
}

.extraTitle {
  font-size: 1.4em;;
}